html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Cairo", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

/** Media Queries **/
.lista__heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
}
.lista__heading span {
  font-weight: 700;
}
.lista__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 20rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #ff0000 0%, #ff0000 50%, #333333 50%, #333333 100%);
}
.lista__tabla {
  width: 100%;
  border: 2px solid #3b4862;
  text-align: center;
  border-collapse: collapse;
}
.lista__thead {
  background-color: #000;
  color: #FFFFFF;
}
.lista__th {
  padding: 1rem 0;
}
.lista__td {
  padding: 1rem;
  font-size: 2rem;
}
.lista__tr:hover {
  background-color: #c2cada;
}
.lista__tbody {
  background-color: #FFFFFF;
}

.logo {
  display: flex;
  position: relative;
}
.logo__texto {
  margin: -1.5rem 0 0 0.7rem;
  font-size: 4rem;
}
.logo__recuadros {
  display: flex;
}
.logo__recuadro {
  width: 40px;
  height: 40px;
  border-radius: 6px;
}
.logo__recuadro:nth-child(1) {
  border: 4px solid #ff0000;
}
.logo__recuadro:nth-child(2) {
  margin-top: 1rem;
  margin-left: -3rem;
  border: 4px solid #000;
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.5) 0%, rgba(59, 72, 98, 0.8) 100%), url("../img/hero.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  max-width: 100%;
  height: 40rem;
}
.header__barra {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__descripcion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__heading {
  margin-top: 0rem;
  margin-bottom: 0rem;
  color: #FFFFFF;
  -webkit-text-stroke: 0.6px #000;
}
@media (min-width: 768px) {
  .header__heading {
    margin-top: 5rem;
    margin-bottom: 0rem;
  }
}
.header__texto {
  margin: 0;
  color: #FFFFFF;
  font-weight: 900;
  text-align: center;
  -webkit-text-stroke: 0.6px #000;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.navegacion__link {
  -webkit-text-stroke: 0.6px #000;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 900;
  margin: 0 2rem;
  margin-bottom: 0.5rem;
  text-decoration: none;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.nosotros {
  background-color: #FFFFFF;
  margin-top: 2rem;
  padding-top: 2rem;
}
.nosotros__heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
  font-size: 3rem;
}
.nosotros__heading span {
  font-weight: 700;
}
.nosotros__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 20rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #ff0000 0%, #ff0000 50%, #333333 50%, #333333 100%);
}
.nosotros__caracteristicas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .nosotros__caracteristicas {
    flex-direction: row;
  }
}
.nosotros__caracteristica {
  max-width: 80rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  background-color: #f8f8f8;
  padding-top: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 2rem;
  margin-bottom: 3rem;
}
.nosotros__caracteristica-heading {
  font-size: 2.5rem;
}
.nosotros__texto {
  text-align: center;
}

.productos {
  margin-top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .productos {
    margin-top: 3rem;
  }
}
.productos__heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
  font-size: 3rem;
}
.productos__heading span {
  font-weight: 700;
}
.productos__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 20rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #ff0000 0%, #ff0000 50%, #333333 50%, #333333 100%);
}
.productos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.producto {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 0 0 0;
  border: 2px solid #6277a1;
  border-radius: 10px;
  padding: 4rem 4rem;
}
.producto__nombre {
  margin-top: 1.5rem;
  text-transform: uppercase;
  margin-bottom: 0;
}
.producto__enlace {
  background-color: #3b4862;
  padding: 1rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .producto__enlace {
    width: auto;
  }
}
.producto__enlace:hover {
  background-color: #FFFFFF;
  border: 2px solid #3b4862;
  color: #3b4862;
}
.producto__imagen {
  aspect-ratio: 8/10;
  border: 1px solid #3b4862;
  border-radius: 3px;
  width: 20rem;
}

.footer {
  background-color: #455472;
  padding: 3rem 0 0 0;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__heading {
  color: #FFFFFF;
  font-size: 2rem;
}
.footer__frase {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
}
.footer__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 1rem;
  text-decoration: none;
}
.footer__link:last-of-type {
  margin-bottom: 0;
}
.footer__copyright {
  background-color: #283142;
  text-align: center;
  padding: 1rem;
  margin: 0;
  color: #FFFFFF;
}
.footer__copyright a {
  color: #FFFFFF;
  text-decoration: none;
}
.footer__nombre-contacto {
  color: #FFFFFF;
  font-size: 2.3rem;
  background-color: #151922;
  text-align: center;
}
.footer__nombre-contacto a {
  color: #FFFFFF;
  text-decoration: none;
}
.footer__logo-iconos {
  margin-top: 2.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.phone-float {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 40px;
  background-color: #25d366;
  color: #FFF;
  border-radius: 50px;
  text-align: center;
  font-size: 30px;
  box-shadow: 2px 2px 3px #999;
  z-index: 100;
}

.my-phone-float {
  margin-top: 16px;
}

.header-productos__heading {
  font-size: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header-productos__heading {
    text-align: left;
    font-size: 5rem;
  }
}
.header-productos__barra {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header-productos__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header-productos__hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header-productos__hero {
    margin-left: 3rem;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.header-productos__textos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-productos__textos {
    padding: 10rem;
    justify-content: center;
    align-items: start;
    margin-bottom: 5rem;
  }
}
.header-productos__texto {
  margin: 0 0;
}
.header-productos__imagen {
  padding: 4rem 0;
  max-width: 40rem;
}
.header-productos .navegacion-productos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header-productos .navegacion-productos {
    flex-direction: row;
    margin-top: 0;
  }
}
.header-productos .navegacion-productos__link {
  color: #000;
  font-size: 2rem;
  font-weight: 400;
  margin: 0 2rem;
  margin-bottom: 0.5rem;
  text-decoration: none;
}
.header-productos .navegacion-productos__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .header-productos .navegacion-productos__link {
    margin-bottom: 0;
  }
}

.lista__heading {
  text-align: center;
  font-size: 2.5rem;
}
.lista__td {
  text-align: center;
  font-size: 1.8rem;
}

.header-digital {
  background-color: #000;
}
.header-digital .logo__recuadro:nth-child(2) {
  border: 3px solid #FFFFFF;
}
.header-digital .logo__texto {
  color: #FFFFFF;
}
.header-digital__barra {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header-digital__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion-digital {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion-digital {
    flex-direction: row;
    margin-top: 0;
  }
}
.navegacion-digital__link {
  -webkit-text-stroke: 0.6px #000;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  margin: 0 1.5rem;
  margin-bottom: 0.5rem;
  text-decoration: none;
}